body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #121212;
  color: #ffffff;
}

.container {
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #1e1e1e;
  border-radius: 10px;
}

input, select, button {
  display: block;
  width: 100%;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  border-radius: 5px;
  border: none;
}

.produto {
  background-color: #2c2c2c;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
}

button {
  background-color: #4caf50;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}
